import React, { PureComponent } from "react";
import CardHover from "./CardHover";
import SocialCardMedia from "./Media";
import CardIcons from "../../Elements/CardIcons";
import SocialShare from "../../Elements/SocialShare";
import { connect } from 'react-redux';
import {findNetworkDataFromWebFilters , findFromArray, removeTransFromCss, isPostAvailable} from '../../../../utils'
import { themePostTracking, showPopUP } from '../../../../../../../../../../actions/themeActions/themeActions'





class Card extends PureComponent {
  constructor(props) {
    super(props);
    this.myRef_collage = React.createRef();
    this.state = {cardWidth : null}
  }

  componentDidMount(){ 
    if(isPostAvailable(this.props.itemData.id)){
      this.onLoadPopup();
      setTimeout(()=>{
        const element = document.querySelector(`.ts_p_${this.props.itemData.id}`);
        if(element){element.scrollIntoView(true);}
      // console.log('width', this.myRef_collage && this.myRef_collage.current ? this.myRef_collage : null)
      if(this.myRef_collage && this.myRef_collage.current) this.setState({cardWidth: this.myRef_collage.current.clientWidth})
      },500)
    }
  }

  componentDidUpdate(){
    setTimeout(() => {
      // console.log('width', this.myRef_collage && this.myRef_collage.current ? this.myRef_collage.current.clientWidth : null)
      if(this.myRef_collage && this.myRef_collage.current) this.setState({cardWidth: this.myRef_collage.current.clientWidth})
    }, 1000);
  }

  onLoadPopup = event => {
    const { itemData, wall, appendData, postData, itemId, languageSetting, completeDataObject, wallId, webFilters } = this.props;
    let updatePostData = postData.map(itemData => completeDataObject[itemData])
    let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
    let currentIndex = findFromArray(filteredPostData, itemData.id, postData.indexOf(itemId))

    if (wall.Personalization.postFeatured === 1) {
      removeTransFromCss(`add`);
      this.props.showPopUP({ type: 'post', card: itemData, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, webFilters: webFilters, UserRule: wall.UserRule, wall: wall })
    } themePostTracking({ action: 2, wall: wallId, feed: itemData.feedId, post: itemData.referenceId })
  }

  render() {   
    
    const { itemData, itemId, wall, webFilters, adjustWidth, cardWidth , fullWidth} = this.props; 
    const highlight = itemData.highlight; const shareOption = wall.ThemeRule.shareOption; const hideContent = wall.ThemeRule.hideContent;
    const networkData =   findNetworkDataFromWebFilters(itemData.network, webFilters)
    const ts_ct_post_in = { backgroundColor: wall.ThemeRule.cardColor, borderRadius: wall.ThemeRule.roundEdge};
    const postWidth = { width: fullWidth? `100%`: cardWidth+`%`, padding: 3 }
    const isShopIcon = (itemData.ugc_products && itemData.ugc_products.length > 0 && itemData.hotspot === 0) ? true : false;  
    const isVideo = (itemData.type===3 || itemData.type === 5) ? true : false;  
    const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
    console.log('cardWidth',itemData.id,  this.state.cardWidth, this.myRef_collage)

    return (
      <div className={`ts_ct_post_wrapper ${itemData && itemData.id ? "ts_p_"+itemData.id:"" }`} ref={this.myRef_collage} style={postWidth} >
        <div className={`ts_ct_post_in ${itemData.hotspot ? '':'ts_ct_post_in_hostpot_hover'}`} onClick={itemData.isPost ? null : this.onLoadPopup} style={ts_ct_post_in}>
          {isShopIcon || isVideo || multiImages? <CardIcons item={itemData} cardWidth={this.state.cardWidth} wall={wall}/> :''}
          <SocialCardMedia  item={itemData} wall={wall} />
          {!itemData.hotspot ? <CardHover cardWidth={this.state.cardWidth} wall={wall} item={itemData} network={networkData}/> : null}
        </div>
      </div>
    );
  }
}



const mapDispatchToProps = dispatch => { return { showPopUP: (data) => dispatch(showPopUP(data)), } }; 
const mapStateToProps = state => { return { wallId: state.appData.wallID, } }
export default connect(mapStateToProps, mapDispatchToProps)(Card);