import React, { Component } from 'react';
import { themePostTracking } from '../../../../../../../../../../actions/themeActions/themeActions'
class SocialShare extends Component {
  // componentDidMount() {
  //   if (window.twttr && window.twttr.ready) window.twttr.ready((twttr) => {
  //     window.twttr.events.bind('tweet', this.onTwitterClick);
  //   });
  // }

  onShareFacebook=event=>{
    const { item, wall } = this.props;
    var url = 'https://www.facebook.com/sharer/sharer.php?display=popup&u=' + item.share.facebook;
    var options = 'toolbar=0,status=0,resizable=1,width=626,height=436';
    window.open(url,'sharer',options);
  }
  
  onTwitterClick = event => {
    const { item, wall } = this.props;
    if (event) {
      themePostTracking({
        type: 2,
        action: 2,
        wall: wall.Wall.id,
        feed: item.feedId,
        post: item.referenceId,
      })
    }
  }
  linkedinShareCount = event => {
    const { item, wall } = this.props;
    themePostTracking({
      type: 2,
      action: 2,
      wall: wall.Wall.id,
      feed: item.feedId,
      post: item.referenceId,
    })
  }

  render() {
    const { network, item, wall } = this.props;
    return <>
      {!(item.share.status === 0 || network.id === 7) ?
         <div className="ts__share-icon_wrapper" style={{ fontSize: 40, color: 'rgb(112, 112, 112)'}} >
           
          <a herf="" target="_blank" >
            <div className="tb__icon tb-share-alt"><div></div></div>
          </a>
          <a onClick={this.onShareFacebook} >
            <div className="tb__icon tb-facebook-f"><div></div></div>
          </a>

          <a  onClick={this.onTwitterClick} href={item.share.twitter}   target="_blank">
            <div className="tb__icon tb-twitter"><div></div></div>
          </a>
          <a onClick={this.linkedinShareCount}  onClick={this.linkedinShareCount}  href={item.share.linkedin} target="_blank">
            <div className="tb__icon tb-linkedin"><div></div></div>
          </a>


        </div>
        : null
      }
    </>
  }
}
export default SocialShare;