import React, { PureComponent } from "react";
import { v4 as uuidv4 } from 'uuid';
import Card from "./Card";

export default class Row extends PureComponent {
    render() {
        const { dataArr, indexNo, completeDataObject, wall, webFilters, languageSetting, cardWidth, largeDiv, smallDiv } = this.props;
        const postPosition =wall.ThemeRule.highlightPosition; const sumPost = indexNo%2;
        const largeDivStyle = { width: largeDiv + `%`,  float: sumPost=='0' ? (postPosition=='right' ? 'right':'left'): postPosition=='right' ? 'left':'right' }; 
        const smallDivStyle = { width: smallDiv + `%` , float: sumPost=='1' ? (postPosition=='right' ? 'left':'right'): postPosition=='right' ? 'left':'right' }
        return (
            <div className="ts_cs_post_row">
                {(dataArr && dataArr.length > 0) ?
                    <>
                        <div className="ts_cs_post_large" style={largeDivStyle}> <Card itemData={completeDataObject[dataArr[0]]} cardWidth={cardWidth} itemId={completeDataObject[dataArr[0]]?.id} fullWidth={true} {...this.props} /> </div>
                        <div className="ts_cs_post_small" style={smallDivStyle}>
                            {
                                (dataArr && dataArr.length > 0) ? dataArr.map((item, index) => {
                                    const cardData = completeDataObject[item];
                                    return (index == 0 ? '' : <Card itemData={cardData} cardWidth={cardWidth}  fullWidth={false}  itemId={cardData.id} key={`post_id_${cardData.id}`}  {...this.props} />)
                                }) : null
                            } </div>
                    </>
                    : ''}
            </div>
        );
    }
}
